import { registerBlockType } from '@wordpress/blocks';

import { __ } from '@wp-plugins/i18n';

import { blockAttributesV1, blockAttributesV0, blockAttributesV2 } from './constants';
import type { TelegramLoginAtts } from './types';
import { Output } from './Output';
import { Edit } from './Edit';

import './style.scss';

const savedSettings = (window['wptelegram_login']?.savedSettings || {}) as TelegramLoginAtts;

registerBlockType<TelegramLoginAtts>('wptelegram/login', {
	title: __('WP Telegram Login'),
	icon: 'smartphone',
	category: 'wptelegram',
	attributes: blockAttributesV2,
	edit: Edit,

	save(props) {
		return <Output attributes={props.attributes} className={null} />;
	},
	deprecated: [
		{
			attributes: blockAttributesV0,
			save(props) {
				return <Output attributes={props.attributes} className={null} />;
			},
		},
		{
			attributes: blockAttributesV1,
			migrate(attributes) {
				return { ...savedSettings, ...attributes };
			},
			save(props) {
				return <Output attributes={props.attributes} className={null} />;
			},
		},
		{
			attributes: blockAttributesV2,
			migrate(attributes) {
				return { ...savedSettings, ...attributes };
			},
			save(props) {
				return <Output attributes={props.attributes} className={null} />;
			},
		},
	],
});
